import BigNumber from 'bignumber.js';
import React, { useEffect } from 'react';
import { validateAddress } from 'iotex-antenna/lib/account/utils';
import debounce from 'lodash.debounce';
import ModalView from './ModalView';
import Loading from './Loading';
import { css } from '../modules/index';
import { useStore } from '../store';
import { observer, useLocalStore, useObserver } from 'mobx-react-lite';
import EthereumConfig from '../EthereumConfig';
import { BigNumberState } from '../store/type';
import { parseNote, toHex } from '../lib/zSnarksWorker';
import Web3 from 'web3';
import LoadingButton from '@mui/lab/LoadingButton';
import { hooks } from '../lib/hooks';
import axios from 'axios'
import { GasPriceOracle } from 'gas-price-oracle';
import { toWei, toBN, BN } from 'web3-utils';
import { FlexBox, Section, Text } from '../modules/globalStyle';
import { isMobile } from "react-device-detect";
import { ethers } from 'ethers';
import { PDFHelper } from './PDFHelper/index';

const formatHex = (data) => {
	return data.slice(0, 15) + '..' + data.slice(-15);
}

export const NodeInfo = observer((props) => {
	const { lang, transaction, god, base } = useStore();

	const store = useLocalStore(() => ({
		address: '',
		isAddressValidate: false,
		note: '',
		noteData: null,
		depositAmount: new BigNumberState({ value: new BigNumber(0) }),
		depositBNBAmount: new BigNumberState({ value: new BigNumber(0) }),
		depositCYCAmount: new BigNumberState({ value: new BigNumber(0) }),
		currentCYCDenomination: new BigNumber(0),
		noteTime: null,
		noteTx: null,
		noteFrom: null,
		noteCommitment: null,
		noteDepositAmount: null,
		noteWithdrawTime: null,
		noteWithdrawTx: null,
		noteWithdrawTo: null,
		noteWithdrawNulliferHash: null,
		noteWithdrawAmount: null,
		reportGenStatus: false,
		isSpent: false,
		poolFee: '',
		noteLeafIndex: -1,
		theNoteInput: null,
		poolId: 0,
		pool: {},
		tokens: null,
		indexOfToken: 0,
		isNoteInfoReceived: false,
		rawUnit: new BigNumber(10).pow(EthereumConfig.tokensOnEthereum[god.currentChain.chainId].CYCToken.decimals),

		init(chainID, isConnected) {
			store.tokens = EthereumConfig.tokensOnEthereum[god.currentChain.chainId].tokens;
		},
		cleanup() {
			store.theNoteInput.value = '';
			store.address = '';
			store.isAddressValidate = false;
			store.note = '';
			store.noteData = null;
			store.noteTime = null;
			store.noteTx = null;
			store.noteFrom = null;
			store.noteCommitment = null;
			store.noteDepositAmount = null;
			store.noteWithdrawTime = null;
			store.noteWithdrawTx = null;
			store.noteWithdrawTo = null;
			store.noteWithdrawNulliferHash = null;
			store.noteWithdrawamount = null;
			store.noteLeafIndex = -1;
		},

		async getDataWithNote(note) {
			store.isNoteInfoReceived = false;

			const tempObject = parseNote(note);
			store.noteData = tempObject;

			if( store.noteData.netId != god.currentChain.chainId )
				return (store.noteData = { error: 1 });

			if (tempObject == "Invalid") {
				store.noteData = null;
				// console.log("Invalid");
				return;
			}
			// console.log("passed");
			this.indexOfToken = tempObject.amount;
			store.pool = EthereumConfig.tokensOnEthereum[tempObject.netId].pools[this.indexOfToken];
			// console.log(store.pool);
			// console.log(this.indexOfToken, '=', tempObject.amount);

			const { isValidRoot, isSpent, leafIndex } = await god.generateMerkleProof({ deposit: tempObject.deposit, currency: 'ETH', amount: this.indexOfToken });
			store.isSpent = isSpent;
			// console.log(isValidRoot, isSpent, leafIndex);
			
			if (leafIndex < 0 || !isValidRoot) {
				return (store.noteData = { error: 1 });
			}

			// if (isSpent) {
			// 	return (store.noteData = { error: 2 });
			// }
			

			// const theEvents = await God.findOneLog(tempObject.deposit, store.pool.address, tempObject.netId);
			// if (theEvents[0]) {
			// 	const theEvent = theEvents[0];
			const theEvent = god.fetchedEventsList[leafIndex];
				store.noteTime = theEvent.timestamp * 1000;
				store.noteTx = theEvent.transactionHash;

				var infura_http = new ethers.providers.JsonRpcProvider(god.currentChain.rpcUrl);
				let tx_receipt = await infura_http.getTransactionReceipt(store.noteTx);
				store.noteFrom = tx_receipt.from;

				store.noteCommitment = theEvent.commitment;
				store.noteDepositAmount = this.indexOfToken + ' ' + 'ETH';

				store.noteLeafIndex = parseInt(leafIndex);

				const relayerStatus = await axios.get(god.CurrentSet.relayer + '/status');
                const { rewardAccount, netId, ethPrices, tornadoServiceFee } = relayerStatus.data;
                
                // console.log('Relay address:', rewardAccount);

				// console.log(expense, feePercent, desiredFee);

				// console.log('store.networkFee', store.networkFee.toString());
				// console.log('store.relayerFee', store.relayerFee.toString());
				if( store.isSpent ) {
					let idx = -1;
					const withdrawEvents = await god.fetchEvents({ type: 'withdraw', currency: 'ETH', amount: this.indexOfToken });
					withdrawEvents.map(({ nullifierHash }, index) => {
						if(nullifierHash == tempObject.deposit.nullifierHash)
						  idx = index;
					});
					const theWithdrawEvent = withdrawEvents[idx];
					store.noteWithdrawTime = (await infura_http.getBlock(theWithdrawEvent.blockNumber)).timestamp * 1000;
					store.noteWithdrawTx = theWithdrawEvent.transactionHash;
					store.noteWithdrawTo = theWithdrawEvent.to;
					store.noteWithdrawNulliferHash = theWithdrawEvent.nullifierHash;
					store.noteWithdrawAmount = this.indexOfToken + ' ' + 'ETH';
				}

				store.isNoteInfoReceived = true;
			// 	if (store.pool.XRCToken) {
			// 		store.depositAmount.setValue(new BigNumber(theEvent.tokenDenomination));
			// 		store.depositAmount.decimals = store.pool.XRCToken.decimals;
			// 	} else {
			// 		store.depositAmount.setValue(new BigNumber(0));
			// 		store.depositAmount.decimals = store.pool.decimals;
			// 	}

				// store.depositBNBAmount.setValue(new BigNumber(theEvent.coinDenomination));
				// store.depositCYCAmount.setValue(theEvent.cycDeno.add(theEvent.anonymityFee));

			// 	this.currentCYCDenomination = new BigNumber(
			// 		await God.asyncCallEthereumContract(this.pool.address, this.pool.abi, 'cycDenomination', true, null)
			// 	);

			// 	this.cycToWithdraw.value = this.currentCYCDenomination.minus(
			// 		this.currentCYCDenomination
			// 			.multipliedBy(new BigNumber(this.anonymityFee).plus(new BigNumber(store.pool.relayerFee || EthereumConfig.relayerFee)))
			// 			.dividedBy(10000)
			// 	);

			// 	this.poolFee = this.currentCYCDenomination
			// 		.multipliedBy(this.anonymityFee / 10000)
			// 		.dividedBy(new BigNumber(10).pow(store.pool.decimals))
			// 		.toFixed(8)
			// 		.toString();
			// } 
			// else {
			// 	store.noteData = { error: 1 };
			// }

			// console.log('isAvailable', store.note && store.noteData && !store.noteData.error && store.noteLeafIndex >= 0 && store.isAddressValidate);
		},

		enableStatus() {
			store.reportGenStatus = true;
			setTimeout(() => {
				store.disableStatus();
			}, 2000);
		},

		disableStatus() {
			store.reportGenStatus = false;
		}

	}));

	const ActionButton = useObserver(() => {
		// if (!props.isAvaliable) {
		//   return <Button avaliable={true} label={lang.t('connect')} onClick={store.onConnect} />;
		// }
		return (
			// <Button
			// 	label={lang.t('WITHDRAW')}
			// 	avaliable={store.note && store.noteData && !store.noteData.error && store.noteLeafIndex >= 0 && store.isAddressValidate ? true : false}
			// 	onClick={store.withdraw}
			// />
			<LoadingButton
                  color='success'
				  disabled={ !store.isNoteInfoReceived || !store.isSpent }
                  variant="contained"
                  onClick={() => {
					if(!store.reportGenStatus)
						store.enableStatus();
				  }}
                  sx={{
                    height: '40px',
                    fontWeight: 'bold',
                    color: !store.isNoteInfoReceived || !store.isSpent ? '#acb3bb !important' : '',
                    background: !store.isNoteInfoReceived || !store.isSpent ? '#b9b9b952 !important' : '',
					maxWidth: '250px'
                  }}
                  fullWidth
                >
                  {/* { store.ApproveStatus == "approved" ? "Approved" : "Approve M87"} */}
				  Generate PDF report
			</LoadingButton>
		);
	});
	// }, [props.isAvaliable, store.note, store.noteData, store.noteLeafIndex, store.isAddressValidate]);

	// useEffect(() => {
	//   setTimeout(() => {
	//     store.connect();
	//   }, 1000);
	// }, [])

	useEffect(() => {
		store.init(props.chainID, props.isAvaliable);
	}, [props.chainID, props.isAvaliable]);

	useEffect(() => {
		store.withdrawStatus = store.note && store.noteData && !store.noteData.error && store.noteLeafIndex >= 0 && store.isAddressValidate ? 'active' : 'notActive';
	}, [ store.note, store.noteData, store.noteLeafIndex, store.isAddressValidate ]);

	return (
		<div
			className={styles.withdraw.className}
			style={{
				height: isMobile ? '750px' : '380px',
				color: '#ffffffc9'
			}}
		>
			{ store.reportGenStatus ?
				<PDFHelper
					noteTime = {new Date(store.noteTime).toUTCString()}
					noteWithdrawTime = {new Date(store.noteWithdrawTime).toUTCString()}
					noteWithdrawAmount = {store.noteWithdrawAmount}
					noteTx = {store.noteTx}
					noteWithdrawTx = {store.noteWithdrawTx}
					noteDepositAmount = {store.noteDepositAmount}
					noteCommitment = {store.noteCommitment}
					noteWithdrawNulliferHash = {store.noteWithdrawNulliferHash}
					noteFrom = {store.noteFrom}
					noteWithdrawTo = {store.noteWithdrawTo}
					note = {store.note}
				/>
				: <></>
			}

			<div className={styles.note.className}>
				<div className="header">{lang.t('note')}</div>
				<div className="flex">
					<input
						ref={(node) => (store.theNoteInput = node)}
						className="input"
						type="text"
						onChange={debounce(() => {
							if( store.withdrawStatus == "loading" )
								return;
							if( store.theNoteInput)
								store.note = store.theNoteInput.value;
							store.noteData = null;
							store.noteTime = null;
							store.noteTx = null;
							store.noteFrom = null;
							store.noteCommitment = null;
							store.noteDepositAmount = null;
							store.noteWithdrawTime = null;
							store.noteWithdrawTx = null;
							store.noteWithdrawTo = null;
							store.noteWithdrawNulliferHash = null;
							store.noteWithdrawamount = null;
							store.noteLeafIndex = -1;

							store.getDataWithNote(store.theNoteInput.value);
						}, 3000)}
						style={{ borderColor: store.noteData && store.noteData.error ? 'red' : '#45bcb8' }}
						placeholder="Please enter your note"
					/>
				</div>
				{store.note &&
					store.noteData && 
					(!store.noteData.error ? (
						store.noteLeafIndex >= 0 && store.isNoteInfoReceived ? (
							<div>
							</div>
						) : (
							<ModalView>
								{ window.scrollTo(0, 0) }
								<Loading text={lang.t('Loading.text8')} />
							</ModalView>
						)
					) : (
						<div
							style={{
								marginTop: '0.5rem',
								fontSize: '12px',
								color: 'red',
							}}
						>
							{lang.t('invalid')}
						</div>
				))}
				<div style={{ marginTop: '0.5rem' }}>
					<FlexBox justify="between" responsive="rc" direction={isMobile ? 'column' : 'row'}>
						<Section css={{ width: '100%', mb: '1.5rem', '@md': { width: '46%', mb: 0 }}}>
							<FlexBox justify="between" responsive="rc" direction={isMobile ? 'column' : 'row'}>
								<Text>Deposit</Text>
								<Text style={{color: '#45BCB8'}}>{ store.noteDepositAmount && store.isNoteInfoReceived ? store.noteDepositAmount : '-' }</Text>
							</FlexBox>
							<FlexBox justify="between" responsive="rc" direction={isMobile ? 'column' : 'row'} style={{marginBottom: 20}}>
								<Text style={{color: '#45BCB8'}}>{ store.isNoteInfoReceived ? 'Verified' : '-'}</Text>
								<Text></Text>
							</FlexBox>
							<FlexBox justify="between" responsive="rc" direction={isMobile ? 'column' : 'row'}>
								<Text>Date</Text>
								<Text style={{color: '#45BCB8'}}>{ store.noteTime && store.isNoteInfoReceived ? new Date(store.noteTime).toUTCString() : '-' }</Text>
							</FlexBox>
							<FlexBox justify="between" responsive="rc" direction={isMobile ? 'column' : 'row'}>
								<Text>Transaction</Text>
								{ store.noteTx && store.isNoteInfoReceived ? 
									<a
										href={
										`https://etherscan.io/tx/` + store.noteTx
										}
										className="colorfulA"
										target="_blank"
										rel="noopener noreferrer"
									>
										{ formatHex(store.noteTx) }
									</a> : 
									<Text style={{color: '#45BCB8'}}>-</Text>
								}
							</FlexBox>
							<FlexBox justify="between" responsive="rc" direction={isMobile ? 'column' : 'row'}>
								<Text>From</Text>
								{ store.noteFrom && store.isNoteInfoReceived ? 
									<a
										href={
										`https://etherscan.io/address/` + store.noteFrom
										}
										className="colorfulA"
										target="_blank"
										rel="noopener noreferrer"
									>
										{ formatHex(store.noteFrom) }
									</a> : 
									<Text style={{color: '#45BCB8'}}>-</Text>
								}
							</FlexBox>
							<FlexBox justify="between" responsive="rc" direction={isMobile ? 'column' : 'row'}>
								<Text>Commitment</Text>
								<Text style={{color: '#45BCB8'}}>{ store.noteCommitment && store.isNoteInfoReceived ? formatHex(store.noteCommitment) : '-' }</Text>
							</FlexBox>
						</Section>
						<Section css={{ width: '100%', mb: '1.5rem', '@md': { width: '46%', mb: 0 }}}>
							<FlexBox justify="between" responsive="rc" direction={isMobile ? 'column' : 'row'}>
								<Text>Withdrawal</Text>
								<Text style={{color: '#45BCB8'}}>{ store.noteWithdrawAmount && store.isNoteInfoReceived ? store.noteWithdrawAmount : '-' }</Text>
							</FlexBox>
							<FlexBox justify="between" responsive="rc" direction={isMobile ? 'column' : 'row'} style={{marginBottom: 20}}>
								{
									store.isNoteInfoReceived ?
									(store.isSpent ? <Text style={{color: '#45BCB8'}}>Verified</Text> : <Text style={{color: 'red'}}>The note has not been spent</Text> ) :
									<Text style={{color: '#45BCB8'}}>-</Text>
								}
								<Text></Text>
							</FlexBox>
							<FlexBox justify="between" responsive="rc" direction={isMobile ? 'column' : 'row'}>
								<Text>Date</Text>
								<Text style={{color: '#45BCB8'}}>{ store.noteWithdrawTime && store.isNoteInfoReceived ? new Date(store.noteWithdrawTime).toUTCString() : '-' }</Text>
							</FlexBox>
							<FlexBox justify="between" responsive="rc" direction={isMobile ? 'column' : 'row'}>
								<Text>Transaction</Text>
								{ store.noteWithdrawTx && store.isNoteInfoReceived ? 
									<a
										href={
										`https://etherscan.io/tx/` + store.noteWithdrawTx
										}
										className="colorfulA"
										target="_blank"
										rel="noopener noreferrer"
									>
										{ formatHex(store.noteWithdrawTx) }
									</a> : 
									<Text style={{color: '#45BCB8'}}>-</Text>
								}
							</FlexBox>
							<FlexBox justify="between" responsive="rc" direction={isMobile ? 'column' : 'row'}>
								<Text>To</Text>
								{ store.noteWithdrawTo && store.isNoteInfoReceived ? 
									<a
										href={
										`https://etherscan.io/address/` + store.noteWithdrawTo
										}
										className="colorfulA"
										target="_blank"
										rel="noopener noreferrer"
									>
										{ formatHex(store.noteWithdrawTo) }
									</a> : 
									<Text style={{color: '#45BCB8'}}>-</Text>
								}
							</FlexBox>
							<FlexBox justify="between" responsive="rc" direction={isMobile ? 'column' : 'row'}>
								<Text>Nullifier Hash</Text>
								<Text style={{color: '#45BCB8'}}>{ store.noteWithdrawNulliferHash && store.isNoteInfoReceived ? formatHex(store.noteWithdrawNulliferHash) : '-' }</Text>
							</FlexBox>
						</Section>
					</FlexBox>
				</div>
			</div>

			<div className={styles.buttonPanel.className}>
				{ActionButton}
			</div>
			
		</div>
	);
});

const styles = {
	withdraw: css({
		maxWidth: '942px',
		display: 'flex',
		justifyContent: 'space-between',
		flexDirection: 'column',
		'@md': {
			height: 'auto',
		},
		margin: 'auto',
		background: '#ffffff1a',
		marginTop: '-30px'
	}),
	note: css({
		marginTop: '1rem',
		marginLeft: '8vw',
		marginBottom: '1.5rem',
		paddingRight: '40px',
		'@md': {
			marginTop: '25px',
			marginLeft: '40px',
		},
		'.note-tips': {
			fontSize: 12,
			marginBottom: 8,
			color: 'red',
		},
	}),
	recipient: css({
		marginLeft: '8vw',
		marginBottom: '2rem',
		paddingRight: '10vw',
		'@md': {
			marginLeft: '40px',
			paddingRight: '40px',
		},
		'.flex': {
			display: 'flex',
			alignItems: 'center',
			justifyContent: 'space-between',
			'.paste': {
				padding: '0 0.25rem',
				cursor: 'pointer',
				fontSize: '14px',
				fontWeight: 500,
				lineHeight: '40px',
				color: '#fff',
				backgroundColor: 'rgb(69, 188, 184)',
			},
		},
		'.recipient-tips': {
			fontSize: 12,
			marginBottom: 8,
			color: 'red',
		},
	}),
	buttonPanel: css({
		width: '100%',
		display: 'flex',
		justifyContent: 'center',
		marginBottom: '1.28rem',
		'.button': {
			width: '50%',
			height: '40px',
			lineHeight: '40px',
			display: 'flex',
			justifyContent: 'center',
			backgroundColor: '#45BCB8',
			color: 'white',
			fontSize: '1rem',
			fontWeight: 'bold',
			cursor: 'pointer',
		},
	}),
};

export default NodeInfo;
